@media screen and (max-width: 620px) { /*当屏幕尺寸小于620px时，应用下面的CSS样式*/
    .wrapper{
        padding:10px 10px;
    }
    .fixed-top{
        width: 100%;
        position: relative;
        top: 0;
        left: 0;
    }
    .row{
        padding: 0px 5px;
        box-sizing: border-box;
    }
    .col{
        display: block !important;
        width: 100% !important;
    }
    .education li{
        float: none;
        padding: 5px;
        width: 100%;
    }
}